import { VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { Component, OnInit } from '@angular/core';
import { FunctionService } from 'src/app/sales/services/function.service';

@Component({
  selector: 'app-sales-program',
  templateUrl: './sales-program.component.html',
  styleUrls: ['./sales-program.component.scss']
})
export class SalesProgramComponent implements OnInit {
  sales_program_scope: any = this.vhQuerySales.getBranch_Management_Scope().sales_program_scope            // cho phép áp dụng công thức chiết khấu cho toàn bộ chi nhánh
  product_scope: any = this.vhQuerySales.getBranch_Management_Scope().product_scope 
  customer_scope: any = this.vhQuerySales.getBranch_Management_Scope().customer_scope 
  supplier_scope: any = this.vhQuerySales.getBranch_Management_Scope().supplier_scope 
  employee_scope: any = this.vhQuerySales.getBranch_Management_Scope().employee_scope 
  useBranch = this.functionService.checkUsingBranch()
  constructor(
    private vhQuerySales: VhQuerySales,
    private vhAuth:VhAuth,
    private functionService:FunctionService
  ) { }

  ngOnInit(): void {
    
    
  }

  updateConfigSalesProgram(value) {
    if(this.vhAuth.checkMyPermission('management_configure_sales_program'))
    this.vhQuerySales.updateBranch_Management_Scope(value).then((bool) => {
      
    })
  }
}
