import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesProgramComponent } from './sales-program.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
const routes: Routes = [
  {
    path: "",
    component: SalesProgramComponent
  },
]


@NgModule({
  declarations: [SalesProgramComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    NgZorroAntModule,
  ]
})
export class SalesProgramModule { }
